(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('QualityFormController', formControllerFunc);

  /** @ngInject */
  function formControllerFunc(formService, $scope, $log, $q, $uibModal, toastr, NgTableParams, modalService, security, $injector, $state, $location) {
    var vm = this;
    var dataForFormModal = {
      formData: {},
      metaData: {}
    };
    var s_deptCode = '',
      s_type = '';

    vm.saveEditForm = saveEditForm;
    vm.saveForm = saveForm;
    vm.edit = edit;
    vm.del = del;
    vm.addForm = addForm;
    vm.addDept = addDept;
    vm.removeDept = removeDept;
    vm.addDetailList = addDetailList;
    vm.removeDetailList = removeDetailList;
    vm.deptCode = security.getUserinfo().role.deptCode;

    //##  
    vm.filter_type = [
      { type: '' },
      { type: '1' }
    ];

    vm.filter_area = [
      { type: '' },
      { type: '1' }
    ];

    vm.filter_type.selected = vm.filter_type[0];
    vm.filter_area.selected = vm.filter_area[0];

    // ?

    initData();

    function initData() {
      formService.getFormList(s_deptCode, s_type).then(function(response) {
        if (response) {
          vm.lst = response.data.data;
          vm.tableParams = new NgTableParams({}, {
            dataset: vm.lst
          });
        }
      });
    }

    // dataForLeaveModal.formData.deptCode = security.getUserinfo().role.deptCode;

    function addForm() {
      dataForFormModal = {
        formData: {},
        metaData: {}
      };
      modalService.open({
        templateUrl: 'app/quality/form/addForm.tpl.html',
        size: 'lg',
        ok: saveForm,
        data: {
          formData: dataForFormModal.formData
        },
        methodsObj: {
          addDept: addDept,
          removeDept: removeDept,
          addDetailList: addDetailList,
          removeDetailList: removeDetailList
        }
      });
    }

    //## -
    function addDept() {
      var m_this = this;

      if (m_this.deptText === '') return;

      m_this.formData.deptList = m_this.formData.deptList || [];

      m_this.formData.deptList.push(m_this.deptText);

      //  ng-model
      m_this.deptText = '';
    }

    //## -
    function removeDept($index) {
      var m_this = this;
      m_this.formData.deptList.splice($index, 1);
    }

    //## -
    function addDetailList(detailList) {
      var m_this = this;

      if (!m_this.detailList) {
        return;
      } else if (!m_this.detailList.detailCode && !m_this.detailList.item && !m_this.detailList.points && !m_this.detailList.stantard) {
        return;
      }

      if (m_this.detailList.item)

        if (!m_this.formData.qualityFormDetailList) {
          m_this.formData.qualityFormDetailList = [];
        }

      m_this.formData.qualityFormDetailList.push(detailList);

      //  ng-model
      m_this.detailList = {};
    }

    //## -
    function removeDetailList($index) {
      var m_this = this;
      m_this.formData.qualityFormDetailList.splice($index, 1);
    }

    //## 
    function saveForm() {
      var data = angular.extend({}, dataForFormModal.formData);

      data.deptCode = data.deptList ? data.deptList.join(',') : '';

      formService.saveForm(data).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          vm.formData = {};
          modalService.close('success');
        }
      });
    }

    //## 
    function edit(row) {
      dataForFormModal.formData = angular.extend({}, row);

      if (row) {
        if (row.deptCode) {
          dataForFormModal.formData.deptList = row.deptCode.split(',');
        }
      }

      modalService.open({
        templateUrl: 'app/quality/form/addForm.tpl.html',
        size: 'lg',
        ok: saveEditForm,
        data: {
          formData: dataForFormModal.formData
        },
        methodsObj: {
          addDept: addDept,
          removeDept: removeDept,
          addDetailList: addDetailList,
          removeDetailList: removeDetailList
        }
      });
    }

    //## 
    function saveEditForm() {
      var data,
        array_length,
        parrent_create_Time,
        child_create_Time;

      data = angular.extend({}, dataForFormModal.formData);

      data.deptCode = data.deptList ? data.deptList.join(',') : '';

      parrent_create_Time = data.createTime;
      data.createTime = moment(parrent_create_Time).format('YYYY-MM-DD hh:mm:ss');

      if (data.qualityFormDetailList) {
        array_length = data.qualityFormDetailList.length;
        for (var i = 0; i < array_length; i++) {
          child_create_Time = data.qualityFormDetailList[i].createTime;
          data.qualityFormDetailList[i].createTime = moment(child_create_Time).format('YYYY-MM-DD hh:mm:ss');
        }
      }

      formService.editForm(data).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          vm.formData = {};
          modalService.close('success');
        }
      });
    }

    //## 
    function del(seqId) {
      formService.deleteForm(seqId).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          //
          if (seqId == vm.formData.seqId) {
            vm.formData = {};
          }
          modalService.close('success');
        }
      });
    }
  }



})();

