(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('NursingWorkersFileController', filesControllerFunc);

  /** @ngInject */
  function filesControllerFunc($rootScope, $scope, $log, $uibModal, deptService, NgTableParams, filesService, toastr) {
    var vm = this;
    var userData;
    var count = 0;

    var dateArr = [
      'birthday',
      'inductionDate',
      'practiceApproveDate',
      'practiceValidDate',
      'graduationDate',
      'startDate',
      'endDate'
    ];

    vm.rootScope = $rootScope;
    vm.scope = $scope;

    vm.isOpen = true;

    deptService.getDeptData().then(function(data) {
      $log.info('', data);
      vm.deptData = data;
    });

    $log.info(count);

    //
    vm.del = del;
    //
    vm.open = open;

    // 
    vm.load = load;

    //
    vm.loadDetail = loadDetail;

    // vm.detail = filesService.userList;
    vm.tableParams = filesService.tableParams;


    function load(deptCode,deptName) {
      vm.selectedDept = deptCode;
      vm.selectedUserCode = false;

      filesService.selectedDeptCode = deptCode;
      filesService.selectedDeptName = deptName;

      filesService.getUserList(deptCode).then(function(data) {
        $log.info('', data);
        // vm.detail = filesService.userList;
        vm.userDataList = data;
        vm.tableParams = filesService.tableParams;
      });
    }

    function open(userCode) {
      filesService.isNew = !userCode;

      if (userCode) {
        loadDetail(userCode, openDialog);
      } else {
        openDialog();
      }

      function openDialog() {
        var modalInstance = $uibModal.open({
          templateUrl: 'app/nursingWorkers/files/modal.tpl.html',
          controller: 'fileModalController',
          backdrop: 'static',
          controllerAs: 'vm',
          animation: true,
          size: 'xlg'
        });
      }
    }

    function del(row) {
      filesService.deleteUser('comUser',row.seqId).then(function (response) {
        if (response) {
          toastr.success('', '');

          // 
          vm.scope.$emit('refreshUserList', vm.selectedDept);
        }
      });
    }

    function loadDetail(userCode, callback) {
      vm.selectedUserCode = userCode;
      filesService.getUserInfo(userCode,'').then(function (data){
          viewDetail(data,callback);
      });
    }

    //
      function viewDetail(data,callback) {
          vm.detail = angular.extend({}, data);

          // 
          var dataTemp = data,
              prop;

          for (prop in dataTemp) {
              if (dataTemp.hasOwnProperty(prop)) {
                  dateArr.forEach(function (dateItem) {
                      if (dataTemp[prop][dateItem]) {
                          dataTemp[prop][dateItem] = new Date(dataTemp[prop][dateItem]);
                      }

                      if (dataTemp[prop].length > 0) {
                          dataTemp[prop].map(function (tempItem) {
                              if (tempItem[dateItem]) {
                                  tempItem[dateItem] = new Date(tempItem[dateItem]);
                              }

                              return tempItem;
                          });
                      }
                  });
              }
          }

          filesService.selectedUser = dataTemp;
          vm.rootScope.$broadcast('resetCount', filesService.selectedUser);

          if (callback) callback();
      }


    // 
    vm.scope.$on('refreshUserList', function (event, deptCode) {
      load(deptCode);
    });

    // 
    vm.scope.$on('refreshUserDetail', function (event, data) {
      vm.selectedUserCode = data.comUser.userCode;//
      viewDetail(data);
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('workManualService', workManualServiceFunc);

  /** @ngInject */
  function workManualServiceFunc($window, apiHost, $http, $log, $q, toastr, security) {
    var service = {
      get: get,
      save: save,
      delete: del
    };

    return service;

    /**
     * [del ]
     * @param  {[string]} seqId []
     * @return {[object]}       [promise]
     */
    function del(seqId) {
      return $http.post(apiHost.manual.workManual.delete, {
          seqId: seqId
        })
        .then(complete);
    }

    /**
     * [save ]
     * @param  {[object]} dataObj [seqId: number; damageLevelName: string; flow: string]
     * @return {[object]}         [promise]
     */
    function save(dataObj) {
      return $http.post(apiHost.manual.workManual.save, dataObj)
        .then(complete);
    }

    /**
     * [get ]
     * @param  {[object]} params []
     * @return {[object]}        [promise]
     */
    function get() {
      var param = {};

      param.deptCode = security.getUserinfo().role.deptCode;

      return $http.get(apiHost.manual.workManual.get, {
          params: param
        })
        .then(complete);
    }

    function complete(response) {
      if (response.data.code === '0') {
        return response;
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('ManualWorkController', workControllerFunc);

  /** @ngInject */
  function workControllerFunc($scope, $log, NgTableParams, $filter, bedService, modalService, toastr, security) {
    var vm = this;

    vm.saveGroup = saveGroup;
    vm.edit = edit;
    vm.deleteGroup = deleteGroup;
    vm.formData = {};
    vm.add = add;
    vm.deptCode = security.getUserinfo().role.deptCode;

    initData();

    function initData() {
      bedService.getGroupList().then(function(response) {
        if (response) {
          vm.lst = response.data.data.lst;
          vm.tableParams = new NgTableParams({}, {
            dataset: vm.lst
          });
        }
      });
    }

    function edit(row) {
      vm.formData = angular.extend({}, row);
      vm.selectedLevel = row;

      if (row) {
        if (row.beds) {
          vm.formData.bedList = row.beds.split(';');
        }
      }
    }

    function add() {
      vm.formData = {};
    }

    /************
     ********
     ************/
    vm.removeFlow = removeFlow;
    vm.addFlow = addFlow;

    function addFlow() {
      if (vm.flowText === '') {
        return;
      }

      vm.formData.bedList = vm.formData.bedList || [];

      vm.formData.bedList.push(vm.flowText);

      //  ng-model
      vm.flowText = '';
    }

    function removeFlow($index) {
      vm.formData.bedList.splice($index, 1);
    }

    function saveGroup() {
      // debugger;
      var data = angular.extend({}, vm.formData);

      data.beds = data.bedList ? data.bedList.join(';') : '';
      data.deptCode = vm.deptCode;
      // return;
      bedService.saveGroup(data).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          vm.formData = {};
          modalService.close('success');
        }
      });
    }

    function deleteGroup(seqId) {
      bedService.deleteGroup(seqId).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          //
          if (seqId == vm.formData.seqId) {
            vm.formData = {};
          }
          modalService.close('success');
        }
      });
    }
  }



})();

