(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('DepartmentManagerLeaveController', leaveControllerFunc);

  /** @ngInject */
  function leaveControllerFunc(leaveService, $scope, $log, $q, moment, toastr, NgTableParams, modalService, security, $injector) {
    var vm = this;
    var formData = {};

    vm.addLeave = open;
    vm.saveLeave = saveLeave;
    vm.edit = edit;
    vm.del = del;
    vm.review = review;


    //## 
    initData();

    function initData() {
      leaveService.getLeaveList().then(function(response) {
        if (response) {
          vm.lst = response.data.data.lst;
          for (var i = 0; i < vm.lst.length; i++) {
            vm.lst[i].startTime = moment(new Date(vm.lst[i].startTime)).format('YYYY-MM-DD HH:MM:SS');
            vm.lst[i].endTime = moment(new Date(vm.lst[i].endTime)).format('YYYY-MM-DD HH:MM:SS');
          }
          vm.tableParams = new NgTableParams({}, {
            dataset: vm.lst
          });
        }
      });
    }

    formData.deptCode = security.getUserinfo().role.deptCode;
    formData.deptName = security.getUserinfo().role.deptName;

    function open() {
      modalService.open({
        templateUrl: 'app/departmentManager/leave/addLeave.tpl.html',
        size: 'md',
        ok: saveLeave,
        data: {
          formData: formData
        }
      });
    }

    // 
    function dater() {

      formData.startDate = new Date(formData.startTime);
      formData.endDate = new Date(formData.endTime);
    }

    //## 
    function edit(row) {
      formData = angular.extend({}, row);
      dater();
      open();
    }

    //## 
    function del(seqId) {
      leaveService.deleteScheduleLeave(seqId).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          modalService.close('success');
        }
      });
    }

    //## 
    function review(row) {
      formData = angular.extend({}, row);

      dater();

      modalService.open({
        templateUrl: 'app/departmentManager/leave/review.tpl.html',
        size: 'md',
        ok: saveReview,
        data: {
          formData: formData
        }
      });
    }

    function saveReview() {
      leaveService.apprvScheduleLeave(formData).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          formData = {};
          modalService.close('success');
        }
      });
    }

    //## 
    function saveLeave() {
      // 
      var init_startDate = formData.startDate,
        init_startTime = formData.startTime,
        init_endDate = formData.endDate,
        init_endTime = formData.endTime;

      // 
      function timer(d, t) {
        var date, time, result;
        date = moment(d).format('YYYY-MM-DD');
        time = moment(t).format('HH:mm:ss');
        result = moment(date + ' ' + time).format('YYYY-MM-DD HH:mm:ss')
        return result;
      }

      // 
      formData.startTime = timer(init_startDate, init_startTime);
      formData.endTime = timer(init_endDate, init_endTime);

      formData.deptCode = security.getUserinfo().role.deptCode;
      formData.deptNme = security.getUserinfo().role.deptNme;
      leaveService.saveScheduleLeave(formData).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          formData = {};
          modalService.close('success');
        }
      });
    }
  }

})();

