(function() {
  'use strict';

  angular
    .module('nqms')
    .config(function(w5cValidatorProvider) {
      w5cValidatorProvider.setRules({
        loginName: {
          required: "",
          pattern: ""
        },
        password: {
          required: "",
          minlength: "{minlength}",
          maxlength: "{maxlength}"
        }
      })
    })
    .controller('ChangeUserController', changeUserControllerFunc);

  function changeUserControllerFunc($uibModalInstance, $location, security, $log, moment, $window, $timeout, toastr) {
    var vm = this;

    vm.cancel = cancel;
    vm.formData = {};
    vm.datepickerOption = {
      dateDisabled: 'disabled',
      formatYear: 'yyyy',
      formatMonth: 'MM',
      formatDay: 'dd',
      initDate: new Date(),
      maxDate: new Date(2020, 5, 22),
      minDate: moment(),
      startingDay: 1
    };

    vm.validationOption = {
      blurTrig: true
    };

    vm.loginBtnStatus = {
      title: '',
      state: ''
    };

    vm.changeUser = function(data) {
      var loginedUser = security.getUserinfo().user;
      $log.info(data);

      vm.loginBtnStatus.title = '...';
      vm.loginBtnStatus.state = 'disabled';

      // 
      if (data.loginName === loginedUser.loginName) {
        toastr.info('', '');
        resetState();

        return;
      }

      security.login(data).then(function(result) {
        var user_info;

        resetState();

        if (result && result.data['code'] == '-1') {
          return;
        }
        //debugger;
        user_info = {
          user: result.data.data.user,
          nav: result.data.data.funs,
          role: result.data.data.role,
          tab: {}
        };

        security.setUserinfo(user_info);
        security.setCacheUser(user_info);

        $uibModalInstance.dismiss('cancel');
        $timeout(function() {
          $location.path('/home');
          $window.location.reload();
        }, 0)
      }, resetState);
    };

    function cancel() {
      $uibModalInstance.dismiss('cancel');
    }

    function resetState() {
      vm.loginBtnStatus.title = '';
      vm.loginBtnStatus.state = '';
    }
  }
})();

