(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('security', securityFunc);

  function securityFunc($http, $log, $window, $location, $timeout, $state, apiHost) {
    var userinfo,
      service = {
        getUserinfo: getUserinfo,
        setUserinfo: setUserinfo,
        clearUserinfo: clearUserinfo,
        getCacheUser: getCacheUser,
        setCacheUser: setCacheUser,
        clearCacheUser: clearCacheUser,
        login: login,
        getUserRoleList: getUserRoleList,
        getNavigation: getNavigation,
        addToBreadcrumb: addToBreadcrumb,
        removeFromBreadcrumb: removeFromBreadcrumb,
        minMode: false // 
      };

    return service;

    function getUserinfo() {
      return userinfo;
    }

    function setUserinfo(user) {
      userinfo = user;
    }

    function clearUserinfo() {
      userinfo = null;
    }

    function getCacheUser() {
      return angular.fromJson($window.sessionStorage.getItem('userinfo'));
    }

    function setCacheUser(user) {
      $window.sessionStorage.setItem('userinfo', angular.toJson(user));
    }

    function clearCacheUser() {
      $window.sessionStorage.removeItem('userinfo');
    }

    // 
    function login(formData) {
      return $http.post(apiHost.system.login, formData);
    }

    // 
    function getUserRoleList(params) {
      return $http.get(apiHost.system.getRoleList, {
        params: params
      });
    }

    // 
    function getNavigation(roleObj) {
      return $http.get(apiHost.system.getNavigation, {
        params: {
          roleCode: roleObj.roleCode,
          userRoleId: roleObj.seqId
        }
      });
    }

    function addToBreadcrumb(key, value) {
      userinfo.tab[key] = value;
      setCacheUser(userinfo);
    }

    function removeFromBreadcrumb(key) {
      delete userinfo.tab[key];
      setCacheUser(userinfo);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    // 
    .factory('myHttpInterceptor', interceptorFunc);

  /** @ngInject */
  function interceptorFunc($rootScope, $log, $q) {
    return {
      // 
      request: function(config) {
        // $log.info('' + config.url, config);
        return config || $q.when(config);
      },

      // 
      requestError: function(rejection) {
        $log.info('' + rejection.statusText, rejection);
        $rootScope.$broadcast('toast', {
          type: 'error',
          content: '',
          title: ''
        });
        return $q.reject(rejection);
      },

      // 
      response: function(response) {
        // $log.info('' + response.config.url, response);
        if (response.data.code == -2) {
          // authfail
          $rootScope.$broadcast('logout');
        }
        if (response.data.code == -1) {
          $rootScope.$broadcast('toast', {
            type: 'error',
            content: response.data.msg
          });
        }
        if (response.data.code && response.data.code != 0) {
          return $q.reject('code error');
        }
        return response || $q.when(response);
      },

      // 
      responseError: function(rejection) {
        $log.info('' + rejection.statusText, rejection);
        $rootScope.$broadcast('toast', {
          type: 'error',
          content: '' + rejection.status + '<br> URL' + (rejection.config && rejection.config.url || '') + '\n ' + (rejection.message || '') + '<br> ',
          title: ''
        });
        return $q.reject(rejection);
      }
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('deptService', deptServiceFunc);

  /** @ngInject */
  /**
   * [deptServiceFunc ]
   * @param  {[Object]} $window [window]
   * @return {[Object]}         []
   */
  function deptServiceFunc($window, $http, apiHost, $log, toastr, security) {
    var userRole = security.getUserinfo()['role'];

    var service = {
      apiHost: apiHost,
      getDeptData: getDeptData,
      deptList: []
    };

    return service;

    function getDeptData() {

      return $http.get(apiHost.user.getDept, {
          params: {
            deptCode: userRole.deptCode
          }
        })
        .then(getDeptListComplete)
        .catch(getDeptListFailed);

      function getDeptListComplete(response) {
        if (response.data.code === '0') {
          // code
          if (!userRole.deptCode) {
            service.deptList = response.data.data;
          }

          return response.data.data;
        } else {
          $log.error('\n' + response.data.msg);
        }
      }

      function getDeptListFailed(error) {
        $log.error('\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

