(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('ExportExcelController', exportExcelController);

  /** @ngInject */
  function exportExcelController($rootScope, $http, exportExcelService) {
    var vm = this;
    var data;

    vm.doExport = doExport;

    /**
     * DOM
     * @return {object} 
     */
    function getDataFromDom() {
      var ths = document.querySelectorAll('.table[st-table] .header-export th');
      var tds = document.querySelectorAll('.table[st-table] tbody td');
      var headerArr;
      var bodyArr;

      headerArr = [].map.call(ths, function(th) {
        return th.textContent;
      });

      bodyArr = [].map.call(tds, function(td) {
        return td.textContent;
      });

      return {
        header: headerArr,
        body: bodyArr
      }
    }

    function doExport(url) {
      var data = getDataFromDom();

      exportExcelService.downloadExcel(url, data).then(function(response) {
        if (response) {
          $rootScope.$broadcast('toast', {
            type: 'success',
            content: ''
          })
        }
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('timestampToString', timestampToString);

  /** @ngInject */
  function timestampToString($filter) {
    var dateFilter = $filter('date');
    var directive = {
      restrict: 'A',
      require: 'ngModel',
      link: function(scope, element, attrs, ngModel) {
        ngModel.$formatters.shift();
        ngModel.$formatters.shift();
        ngModel.$parsers.shift();
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('CoreController', CoreController);

  /** @ngInject */
  function CoreController($rootScope, $scope, $state, toastr, security) {
    var vm = this;

    $scope.$on('toast', function(event, info) {
      toastr[info.type](info.content || '', info.title || '');
    });

    $scope.$on('logout', function(event) {
      security.clearUserinfo();
      security.clearCacheUser();
      $state.go('login');
    });

    vm.limitEvent = function(e) {
      e.stopPropagation();
      e.preventDefault();
    }
  }

})();

