(function() {
  'use strict';

  angular
    .module('nqms')
    .config(function(w5cValidatorProvider) {
      w5cValidatorProvider.setRules({
        loginName: {
          required: "",
          pattern: ""
        },
        password: {
          required: "",
          minlength: "{minlength}",
          maxlength: "{maxlength}"
        }
      });
    })
    .controller('LoginController', loginController);

  /** @ngInject */
  function loginController($rootScope, $log, $state, security) {
    var vm = this;

    vm.loginBtnStatus = {
      title: '',
      state: ''
    };

    vm.login = function(data) {
      vm.loginBtnStatus.title = '...';
      vm.loginBtnStatus.state = 'disabled';

      security.login(data)
        .then(function(result) {
          //debugger;
          var user_info = {
            user: result.data.data.user,
            nav: result.data.data.funs,
            role: result.data.data.role,
            tab: {}
          };

          security.setUserinfo(user_info);
          security.setCacheUser(user_info);

          // 
          $rootScope.$broadcast('toast', {
            type: 'success',
            content: ''
          });
          $state.go('home');
        })
        .finally(setLoginBtnstate);
    };

    vm.validationOption = {
      blurTrig: true
    };

    function setLoginBtnstate() {
      vm.loginBtnStatus.title = '';
      vm.loginBtnStatus.state = '';
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .run(runBlock);

  /** @ngInject */
  function runBlock($window, $state, $rootScope, security) {
    var userinfo = security.getCacheUser();
    if (userinfo) {
      security.setUserinfo(userinfo);
    } else {
      $state.go('login');
    }

    $rootScope.minMode = false;
  }

})();

